<template>
  <div>
    <span v-if="message">{{ message }}</span>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Access",
  data() {
    return {
      platform: "",
      linkContent: "",
      message: "",
    };
  },
  created() {
    const userAgent = navigator.userAgent.toLowerCase();
    if (/iphone|ipad|ipod/.test(userAgent)) {
      // iOS
      this.platform = "IOS";
    } else if (/android/.test(userAgent)) {
      // Android
      this.platform = "Android";
    } else {
      this.platform = "Web";
    }
  },
  mounted() {
    this.linkContent = this.$getCookie("link");
    if (!this.linkContent) {
      this.linkContent = "no cookie";
    }

    // this.message = `sent ${linkContent} to ${platform}`;
    this.getIpAddress()
      .then((ip) => {
        const bi = {
          // timestamp: this.getTimeStamp(),
          // userAgent: this.getUserAgent(),
          screenSize: this.getScreenSize(),
          platform: this.platform,
          language: this.getLanguage(),
          // fonts: this.getFonts(),
          cookie: decodeURIComponent(document.cookie),
          ip: ip,
        };

        this.createBrowserFingerprint(bi).then((hash) => {
          this.message = hash;
          this.toAppScript(hash);
        });
      })
      .catch((error) => {
        console.error("Error getting IP: ", error);
        // Handle the error
      });
  },
  methods: {
    toAppScript(content) {
      if (this.platform === "IOS") {
        if (window.webkit) {
          window.webkit.messageHandler.getAppLink.postMessage(content);
        }
      }
      if (this.platform === "Android") {
        if (window.Android) {
          window.Android.getAppLink(content);
        }
      }
    },
    getTimeStamp() {
      return new Date();
    },
    getUserAgent() {
      return window.navigator.userAgent;
    },
    getScreenSize() {
      return {
        width: window.screen.width,
        height: window.screen.height,
      };
    },
    getLanguage() {
      return navigator.language || navigator.userLanguage;
    },
    getFonts() {
      const fonts = [];
      var testText =
        "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
      for (let j = 0; j < testText.length; j++) {
        let font = testText[j] + " font";
        let width = this.measureTextWidth(testText[j], font);
        fonts.push({
          font: font,
          width: width,
        });
      }
      return fonts;
    },
    async getIpAddress() {
      try {
        const response = await axios.get("https://api.ipify.org?format=json");
        return response.data.ip;
      } catch (error) {
        console.error("Error: ", error);
        throw error; // Propagate the error
      }
    },
    measureTextWidth(text, font) {
      const canvas = document.createElement("canvas");
      const context = canvas.getContext("2d");
      context.font = font;
      const metrics = context.measureText(text);
      return metrics.width;
    },
    async createBrowserFingerprint(browserInfo) {
      // 將瀏覽器資訊轉換為 JSON 字串
      const input = JSON.stringify(browserInfo);

      const textAsBuffer = new TextEncoder().encode(input);
      const hashBuffer = await window.crypto.subtle.digest(
        "SHA-256",
        textAsBuffer
      );
      const hashArray = Array.from(new Uint8Array(hashBuffer));
      const hash = hashArray
        .map((item) => item.toString(16).padStart(2, "0"))
        .join("");
      return hash;
    },
  },
};
</script>
